<template>
  <div class="api">
  	<h2>Geolocation API</h2>

	<div class="api-info">
		<h3>Geolocation API</h3>
		<p>The Geolocation API allows us to obtain information about the current geographic position of the user. We access the geolocation feature through the global navigator.geolocation property, which returns a Geolocation object.</p>
	</div>
	<pre class="api-code">
	navigator.geolocation; // Geolocation object
	</pre>
	<h4 class="api-table-caption">The Methods</h4>
	<table class="api-table">
		<tr>
			<td>getCurrentPosition(callback, errorCallback, options) [Returns void]</td>
			<td>Get the current position. Only the first attribute is required for the method to work properly.</td>
		</tr>
		<tr>
			<td>watchPosition(callback, errorCallback, options) [Returns number]</td>
			<td>This is similar to the previous method, except that it will start a watch process for the detection of new locations. This method takes the same arguments as the getCurrentPosition method and works in the same way – the difference is that the callback functions will be repeatedly called as the position changes.</td>
		</tr>
		<tr>
			<td>clearWatch(id) [Returns void]</td>
			<td>cancels the process started by the watchPosition() method. id - the identification returned by watchPosition() when it was called.</td>
		</tr>
	</table>
	<pre class="api-code">
	navigator.geolocation.getCurrentPosition(callbackFunction, errorCallback, options); 
	var watchID = navigator.geolocation.watchPosition(callbackFunction, handleError, options); 
	navigator.geolocation.clearWatch(watchID);
	</pre>
	<div class="api-info">
		<h3>The callback attribute</h3>
		<p>callback - callback function - object called Position that holds all the information retrieved by the geolocation systems.</p>
	</div>
	<h4 class="api-table-caption">The Position object attributes</h4>
	<table class="api-table">
		<tr>
			<td>coords</td>
			<td>Returns the coordinates for the current position (Coordinates object)</td>
		</tr>
		<tr>
			<td>timestamp</td>
			<td>This indicates the time when the information was acquired.</td>
		</tr>
	</table>
	<pre class="api-code">
	navigator.geolocation.getCurrentPosition(callbackFunction, errorCallback, options); 
	function callbackFunction(position){ 
	    //position.coords.latitude; 
	    //position.coords.longitude; 
	    //position.coords.accuracy; 
	    //position.coords.altitudeAccuracy; 
	}
	</pre>

	<h4 class="api-table-caption">Coordinates object</h4>
	<table class="api-table">
		<tr>
			<td>latitude</td>
			<td>Returns the latitude in decimal degrees number</td>
		</tr>
		<tr>
			<td>longitude</td>
			<td>Returns the longitude in decimal degrees number</td>
		</tr>
		<tr>
			<td>altitude</td>
			<td>Returns the height in meters number</td>
		</tr>
		<tr>
			<td>accuracy</td>
			<td>Returns the accuracy of the coordinates in meters number</td>
		</tr>
		<tr>
			<td>altitudeAccuracy</td>
			<td>Returns the accuracy of the altitude in meters number</td>
		</tr>
		<tr>
			<td>heading</td>
			<td>Returns the direction of travel in degrees number</td>
		</tr>
		<tr>
			<td>speed</td>
			<td>Returns the speed of travel in meters/second number</td>
		</tr>
	</table>
	<div class="api-info">
		<h3>The errorCallback attribute</h3>
		<p>errorCallback – is callback function, (PossitionError object)</p>
	</div>	
	<h4 class="api-table-caption">The PossitionError object</h4>
	<table class="api-table">
		<tr>
			<td>code</td>
			<td>Returns a code indicating the type of error number</td>
		</tr>
		<tr>
			<td>message</td>
			<td>Returns a string that describes the error</td>
		</tr>
	</table>
	
	<pre class="api-code">
	navigator.geolocation.getCurrentPosition(callbackFunction, errorCallback, options); 
	function errorCallback(error){ 
	    document.innerHTML='Error: '+error.code+' '+error.message; 
	}
	</pre>

	<h4 class="api-table-caption">The PossitionError.code property</h4>
	<table class="api-table">
		<tr>
			<td>0</td>
			<td>When this is returned by PositionError it means than an error not covered by the definition occurs.</td>
		</tr>
		<tr>
			<td>1</td>
			<td>The user did not grant permission to use the geolocation feature</td>
		</tr>
		<tr>
			<td>2</td>
			<td>The position could not be determined</td>
		</tr>
		<tr>
			<td>3</td>
			<td>The attempt to request the location timed out</td>
		</tr>
	</table>
	<div class="api-info">
		<h3>The options attribute</h3>		
		<p>The third possible value of the getCurrentPosition() method is an object containing up to three possible attributes:</p>
	</div>
	<table class="api-table">
		<tr>
			<td>enableHighAccuracy</td>
			<td>Tells the browser that we would like the best possible result</td>
		</tr>
		<tr>
			<td>timeout</td>
			<td>Sets a limit on how many milliseconds a position request can take before a timeout error is reported</td>
		</tr>
		<tr>
			<td>maximumAge</td>
			<td>Tells the browser that we are willing to accept a cached location, as long as it is no older than the specified number of milliseconds</td>
		</tr>
	</table>
	
	<pre class="api-code">
	var options = { 
	   enableHighAccuracy: false, 
	   timeout: 2000, 
	   maximumAge: 3000, 
	} 
	navigator.geolocation.getCurrentPosition(callbackFunction, errorCallback, options);
	</pre>

  </div>
</template>
<script>
export default {
  name: 'geolocation',
  data () {
  	return {}
  }
}
</script>



